.MarkdownNote {
  display: grid;
  height: 100%;
  grid-template-columns: auto;
  grid-template-rows: 40px auto;
  grid-gap: 20px;

  &__title {
    position: relative;
    height: 100%;
    margin: 0;
    color: $color__dark;
    font-weight: 700;
    font-size: 20px;

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 50px;
      height: 4px;
      background-color: $color__light;
    }
  }

  &__content {
    overflow: auto;
  }
}

.MarkdownNote__content {
  font-family: $font__main;
  color: $color__text-default;

  h1 {
    font-size: 36px;
    margin: 0 0 10px 0;
    color: $BURNTORANGE;
  }

  h2 {
    font-size: 30px;
    margin: 30px 0 10px 0;
    color: $BURNTORANGE;
  }

  h3 {
    font-size: 24px;
    margin: 30px 0 10px 0;
    color: $color__dark;
    text-transform: uppercase;
  }

  h4 {
    font-size: 20px;
    margin: 20px 0 10px 0;
  }

  h5 {
    font-size: 18px;
    margin: 0 0 10px 0;
  }

  h6 {
    font-size: 18px;
    margin: 0 0 10px 0;
    color: $color__light;
  }

  p {
    margin: 0 0 10px 0;
    max-width: 700px;
    font-size: 16px;
    line-height: $font__line-height-body;
  }

  strong {
    font-weight: 700;
  }

  em {
    font-style: italic;
  }

  ul,
  ol {
    margin: 0 0 10px 0;
    padding: 0 0 0 20px;
  }

  li {
    line-height: $font__line-height-body;
  }

  pre,
  code {
    font-family: $font__code;
  }

  p > code,
  li > code {
    font-size: 16px;
    padding: 4px 10px;
    color: $color__dark;
    background-color: $CAPEHONEY;
    border-radius: 3px;
  }

  pre {
    margin: 10px;
    padding: 10px 20px;
    color: $color__dark;
    background-color: $CAPEHONEY;
    border-radius: 3px;
    font-size: 18px;
    line-height: $font__line-height-body;
  }

  blockquote {
    max-width: 700px;
    margin: 20px 10px;
    padding: 10px;
    background: $color__off-white;
    border-left: 10px solid $BURNTORANGE;

    > p {
      display: inline;
    }

    > p:last-child {
      margin-bottom: 0;
    }
  }

  a {
    display: inline-block;
    color: $BURNTORANGE;
    text-decoration: none;
    border-bottom: 2px solid;
    padding: 0.1em 0.2em 0;
    margin: 0 -0.1em 1em;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: currentColor;
      opacity: 0;
      transition: opacity 0.2s;
    }

    &:hover::before {
      opacity: 0.1;
    }

    &:active::before {
      opacity: 0.2;
      transition-duration: 0s;
    }
  }

  hr {
    margin: 20px;
    border: 0;
    border-top: 2px solid $BURNTORANGE;
  }

  img {
    display: block;
  }

  table {
    margin: 0 0 20px 0;
    border-spacing: 0;
    border-collapse: collapse;
  }

  table > thead > tr > th {
    vertical-align: bottom;
    border-bottom: 2px solid $color__light;
  }

  table > thead > tr > th,
  table > tbody > tr > th,
  table > tfoot > tr > th,
  table > thead > tr > td,
  table > tbody > tr > td,
  table > tfoot > tr > td {
      padding: 8px;
      line-height: 1.42857143;
      vertical-align: top;
      border-top: 1px solid $color__light;
  }

  table > caption + thead > tr:first-child > th,
  table > colgroup + thead > tr:first-child > th,
  table > thead:first-child > tr:first-child > th,
  table > caption + thead > tr:first-child > td,
  table > colgroup + thead > tr:first-child > td,
  table > thead:first-child > tr:first-child > td {
      border-top: 0;
  }

  table > tbody > tr:nth-child(odd) > td,
  table > tbody > tr:nth-child(odd) > th {
      background-color: $color__off-white;
  }

  th {
    text-align: left;
  }
}
